.PnnlTimeline {
  padding: 0;
  margin: 12px 0;
  position: relative;
}
.PnnlTimelineItem {
  list-style: none;
  position: relative;
  margin: 0;
  padding: 8px 15px;
}
.PnnlTimelineItem + .PnnlTimelineItem {
  border-top: 1px solid rgba(179, 179, 179, 0.5);
}
.PnnlTimelineItem:after {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 29px;
  width: 2px;
  background-color: #b3b3b3;
  content: " ";
  display: block;
  z-index: 0;
}
.PnnlTimelineItem:first-child:after {
  top: 25px;
}
.PnnlTimelineItem:last-child:after {
  bottom: auto;
  height: 25px;
}
.PnnlTimelineItem:only-child:after {
  bottom: auto;
  height: 0px;
}
.PnnlTimelineItem > .PnnlAvatar {
  border: 1px solid white;
  position: absolute;
  top: 8px;
  left: 15px;
  z-index: 1;
}
.PnnlTimelineItem > .PnnlAvatar + div {
  margin-left: 45px;
  position: relative;
  z-index: 1;
}
.PnnlTimelineItem > div h3 {
  font-size: 16px;
  font-weight: 500;
  margin-top: 2px;
  margin-bottom: 0;
  line-height: 16px;
}
.PnnlTimelineItem > div .timestamp {
  font-size: 12px;
  color: #b3b3b3;
  line-height: 22px;
  margin-bottom: 6px;
}
/* We want the default presentation to be edge-to-edge, so make it easy to achieve that in common situations */
.PnnlSidebar .PnnlTimeline,
.popover-content .PnnlTimeline,
.PnnlCard-body .PnnlTimeline {
  margin-left: -15px;
  margin-right: -15px;
}
