input[type="text"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
.titlebar {
  background: white;
}
.titlebar h1 {
  color: #d77600;
}
table {
  border-collapse: collapse;
  width: 100%;
}
td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}
tr:nth-child(even) {
  background-color: #dddddd;
}
.box {
  display: inline-block;
  width: 200px;
  height: 100px;
  margin: 1em;
}
.tableCard {
  width: 22%;
  display: inline-flex;
}
.inline {
  display: inline-block;
  vertical-align: top;
}
.reportImage {
  width: 78%;
  display: inline-block;
  vertical-align: top;
}
img {
  max-width: 100%;
  max-height: 100%;
}
.alert {
  width: 50%;
  height: 50%;
  left: 50%;
  top: 50%;
}
ModalImage {
  max-width: 100%;
  max-height: 100%;
  z-index: 5000;
}
.select {
  width: 25%;
  height: 10%;
}
#chart {
  background-color: #ffffff;
}
.bar {
  shape-rendering: crispEdges;
}
.bar-label {
  fill: #000;
  text-anchor: middle;
  font-size: 18px;
}
.axis path,
.axis line {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}
.gridline path,
.gridline line {
  fill: none;
  stroke: rgba(204, 204, 204, 0);
  shape-rendering: crispEdges;
}
.pagination li:hover:not(.active) a {
  background-color: #d6781e;
}
.table th {
  align-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 1px 1px -1px rgba(0, 0, 0, 0.4);
}
.table td {
  text-align: center;
}
.table thead > tr {
  color: #000;
}
hr {
  margin: 0.1em auto;
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  padding: 0;
}
.tab {
  display: inline-block;
  margin-left: 40px;
}
.CardOverflowScoll {
  overflow: scroll;
}
.SampleTable {
  height: 75vh;
  overflow: scroll;
  overflow-y: scroll;
}
.no-padding {
  padding: 0px;
  align-content: center;
}
.SideScroll {
  white-space: nowrap;
  overflow: scroll;
  overflow-x: scroll;
  display: inline-block;
  width: 125%;
  margin-left: -15%;
}

